import { privateAPi } from ".";

export const tablesApi = privateAPi.injectEndpoints({
  endpoints: (build) => ({
    getTables: build.query({
      query: () => "/dashboard/tables/",
      providesTags: ["tables"],

      pollingInterval: 5000,
    }),
  }),
});

export const { useGetTablesQuery } = tablesApi;
